<script>
    import vSelect from 'vue-select';
    import {AtomSpinner} from 'epic-spinners';
    import TreeElement from "./TreeElement";
    import {EventBus} from '../main'
    import debounce from 'lodash.debounce'
	import {dragAndDrop} from "../mixins/dragAndDrop";

	export default {
        name: "Tree",
        data() {
            return {
                items: [],
                treeMask: [],
	            resultItems: [],
                filter: '',
	            filterActive: false,
                increment: 0,
                table_fields: {},
                questionnaire_blocks: [
                    {
                        id: 100000,
                        block_name: 'Block 1',
                        block_code: 'block1',
                        sets: [
                            // {
                            //     set_name: 'Набор 1',
                            //     set_code: 'set1',
                            //     set_id: 1,
                            //     questions: [],
                            //     visible: true
                            // },
                            // {
                            //     set_name: 'Набор 2',
                            //     set_code: 'set2',
                            //     set_id: 2,
                            //     questions: [],
                            //     visible: true
                            // },
                            // {
                            //     set_name: 'Набор 3',
                            //     set_code: 'set3',
                            //     set_id: 3,
                            //     questions: [],
                            //     visible: true
                            // }
                        ],
                        visible: true
                    }
                ],


                question_types: [
                    {name: 'text', id: 1},
                    {name: 'textarea', id: 2},
                    {name: 'checkbox', id: 3},
                    {name: 'radio', id: 4},
                    {name: 'dropdown', id: 5},
                    {name: 'date', id: 6},
                    {name: 'table', id: 7},
                ],
                width_select_options: [
                    {
                        width: '25%',
                    }, {
                        width: '50%',
                    }, {
                        width: '75%',
                    }, {
                        width: '100%',
                    }
                ],
                validation_options: [
                    {
                        validation_name: 'validation1',
                        id: 1
                    }, {
                        validation_name: 'validation2',
                        id: 2
                    }, {
                        validation_name: 'validation3',
                        id: 3
                    }, {
                        validation_name: 'validation4',
                        id: 4
                    },

                ],
                vselect: null,

                form_parameters: {},

                main_data_models: {},
                fields_parameters : {},
                loading: true,
                checkedItems: {},
            }

        },
		
        methods: {
         getSelectValueTree(item,field){
	            let answer = '';
				this.add_models[field.options_data.options_data_model].forEach(e=>{
				    if(e[field.options_data.options_field_id] === item[field.key]){
                        answer = e[field.options_data.options_field_title]
				    }
				})
	            return answer;
            },
            searchTree: debounce(function(){
                let self = this;
                console.time("Поиск Дерево");
                let subStringFind = toString(this.filter);
                resetBacklight(this.resultItems);
                resetShowFound(this.resultItems);
                if(subStringFind.length === 0){
                    this.filterActive = false;
                    return false;
                }
                this.filterActive = true;
                // Перевод значение в строку
                function toString(string){
                    if(string){
                        return string.toString().toLocaleLowerCase();
                    }else{
                        return '';
                    }
                }
                // Открыть всех родителей
                function showParent(item){
                    self.$set(item, 'visible', true)
                    self.$set(item, 'show', true)
                    if(item.parent) showParent(item.parent);
                }
                // Спрятать всех родитилей
                function hideParent(item){
                    item.visible = false;
                    item.show = false;
                    item.parent ? hideParent(item.parent) : false
                }
                // Показать найденные
                function showFound(resultItems){
                    resultItems.forEach(item=>{
                        item.parent ? showParent(item.parent) : false;
                    })
                }
                // Сбросить найденные
                function resetShowFound(items) {
                    items.forEach(item=>{
                        item.parent ? hideParent(item.parent) : false;
                    })
                }
                // Поиск подстроки в одномерном массиве
                function filterList(){
                    return this.treeMask.filter(item=>{
                        let values = Object.values(item);
                        return  values.some(val=>toString(val).indexOf(subStringFind) !== -1)
                    })
                }
                // Сбросить подстветку
                function resetBacklight(items){
                    items.forEach(e=>e.backlight = false)
                }
                // Установить подстветку
                function backlight(items){
                    items.forEach(e=>self.$set(e, 'backlight', true))
                }

                let resultItems = filterList.call(this);
                backlight(resultItems);
                showFound.call(this,resultItems);

                this.resultItems = resultItems;
                console.timeEnd("Поиск Дерево");
            },1000) ,
            async componentData(Request, params = null) {
                let res = await this.$http.post(Request, params);
                
                this.form_parameters = res.data.form_parameters;
                
                this.$store.commit('formParameters',this.form_parameters);
                
                this.main_data_models = res.data.main_data_models;
                this.table_fields = res.data.table_fields;
                this.fields_parameters = res.data.fields_parameters;
                
                this.items = this.main_data_models[this.form_parameters.form_base_data_model];
                
                this.$store.commit('setFormParameters', {
                    table_fields: res.data.table_fields,
                    fields_parameters: this.fields_parameters,
                    depths_parameters: res.data.depths_parameters
                });
	            
	            this.$nextTick(e=>{
                    this.loading = false;
	            })
            },
            createNewSet() {
                return {
                    set_name: 'Набор ' + this.increment,
                    set_code: 'set1',
                    set_id: this.increment++,
                    questions: [],
                    visible: true
                }
            },

            createNewQuestion() {
                return {
                    id: this.increment++,
                    question_name: null,
                    question_type_name: 'text',
                    question_type_id: 1,
                    status: 'new',
                    question_width: '50%',
                    required_l: false,
                    answers: [{answer: '', id: this.increment++}]
                }

            },

            addAnswer(question) {
                question.answers.push({answer: '', id: this.increment++})
            },

            addQuestion(set) {
                set.questions.push(this.createNewQuestion());
            },

            addSet(block) {
                block.sets.push(this.createNewSet());
            },

            questionTypeChanged(val) {
                console.log(val);
            },

            deleteQuestion(set, question) {
                let question_index = set.questions.findIndex(q => q === question);
                set.questions.splice(question_index, 1);
            },

            copyQuestion(set, question) {
                let new_question = JSON.parse(JSON.stringify(question));
                new_question.id = this.increment++;
                new_question.answers.forEach(answer => answer.id = this.increment++);
                set.questions.push(new_question);
            },

            deleteAnswerVariant(question, answer) {
                let answer_index = question.answers.findIndex(a => a.answer === answer);
                question.answers.splice(answer_index, 1);
            },

            close(element, e) {
                element.visible = !element.visible
            },

            beforeEnter(el) {
                el.style.height = '0';
                el.style.overflow = 'hidden';
            },

            enter(el) {
                el.style.height = `${el.scrollHeight}px`;
            },

            afterEnter(el) {
                el.style.height = "auto";
                el.style.overflow = 'visible';

            },

            beforeLeave(el) {
                el.style.height = `${el.scrollHeight}px`;
            },

            leave(el) {
                setTimeout(() => {
                    el.style.height = '0';
                    el.style.overflow = 'hidden';

                }, 0)
            },


        },
		
	    created(){
            EventBus.$on('treeMask',(payload)=>{
	            this.treeMask.push(payload);
            })
            EventBus.$on('treeSelectModel',(payload)=>{
                let modeAdd = payload.modeAdd;
                let keys = Object.keys(payload.items)
	            
	            // Проверка на наличие модели в Объекте выделеных строк
	            keys.forEach(e=>{
                    this.checkedItems.hasOwnProperty(e) ?
	                    splitValues(this.checkedItems,e,payload.items[e]) :
                        this.$set(this.checkedItems,e,payload.items[e]);
	            })
	            
	            // Если модель пустая, то удалить её из объекта
	            function checkEmptyModel(object,key) {
		            let length = object[key].length;
		            length === 0 ? delete object[key] : false;
                }
                
                // Удаление/Добавление в массив значение
	            function splitValues(object,key,values) {
                    values.forEach(val=>{
                        let includeValue = object[key].includes(val);
                        if(!includeValue && modeAdd){
                            object[key].push(val)
                        }else if(includeValue && !modeAdd){
                            let index =  object[key].indexOf(val);
                            object[key].splice(index,1);
                            checkEmptyModel(object,key);
                        }
                    })
                }
            })
	    },
	    
        computed: {
    
        },

        components: {
            vSelect,
            TreeElement,
            AtomSpinner
        },

        mounted() {
            this.componentData(this.$bRoute.meta.route,{id:this.$bRoute.params[this.$bRoute.meta['id_field']]})
	        this.$nextTick(e=>{
                this.imgtosvg();
	        })
        }
    }
</script>

