<template>
    <li >
        <div class="tree-element-header" :data-depth="depth" :class="{backlight: item.backlight === true,dragAndDropLine : this.editableFields.parameters['drag_and_drop']}">
            <div class="checkbox-wrapper" @click.stop>
                <div class="list_checkbox checkbox checkbox-all">
                    <label :for="model_name+'_'+item.id">
                        <input type="checkbox" @click="selectLevel" :checked="item.checked"  :id="model_name+'_'+item.id">
                        <span class="checkbox-custom">
                            <i class="fa fa-check"></i>
                        </span>
                    </label>
                </div>
            </div>
            <div class="left-part" :style="{'padding-left':2*(depth-1)+'rem'}">
                <div class="header-wrapper">
                    <h2 @click="goToCard">{{title}}</h2>
                    <div @click.stop="editLine" class="add-question">
                        <svg>
                            <path xmlns="http://www.w3.org/2000/svg"
                                  d="M12,2 C6.48,2 2,6.48 2,12 C2,17.52 6.48,22 12,22 C17.52,22 22,17.52 22,12 C22,6.48 17.52,2 12,2 L12,2 Z M17,13 L13,13 L13,17 L11,17 L11,13 L7,13 L7,11 L11,11 L11,7 L13,7 L13,11 L17,11 L17,13 L17,13 Z"
                                  id="Shape-Copy-3" fill="#000000"
                            ></path>
                        </svg>
                    </div>
                    <div @click.stop="toggleLevel(item)" class="collapse-item" :class="{up:item.visible}" v-if="itemChildren.length > 0">
                        <img src="/img/dropdown-i.png" alt="">
                    </div>
                </div>
            </div>
            <div class="right-part">
                <table>
                    <tr>
                        <td v-for="field in table_fields">{{ cellContent(field, item)}}</td>
                    </tr>
                </table>
            </div>
        </div>
        <transition name="tree-transition"
                    @before-enter="beforeEnter"
                    @enter="enter"
                    :css="false"
                    @leave="leave"
                    @after-enter="afterEnter"
                    @beforeLeave="beforeLeave"
        >
            <transition-group class="tree-element-body"
                              v-if="itemChildren && itemChildren.length"
                              v-show="item.visible === true"
                              tag="ul"
                              :css="false"
                              @before-enter="beforeEnter"
                              @enter="enter"
                              @leave="leave"
                              @after-enter="afterEnter"
                              @beforeLeave="beforeLeave"
                              name="answer-transition">
                <template v-for="childKey in itemChildren">
	           
                    <TreeElement :item="itemTree"
                                 :model_name="childKey"
                                 v-show="itemTree.show !== false"
                                 :depth="element_depth"
                                 :key="childKey+itemTree.id+depth+title+itemTree.id"
                                 :parent="item"
                                 :data-index="index"
                                 v-for="(itemTree,index) in item[childKey]"></TreeElement>
                </template>


            </transition-group>
        </transition>
    </li>
</template>

<script>
    import TreeElement from './TreeElement.vue'
    import {EventBus} from '../main'
    import Velocity from 'velocity-animate'

    export default {
        name: "TreeElement",
        data() {
            return {
                table_fields: this.$store.getters.getTableFields,
				animation: false,
            }
        },
	    mounted(){
            EventBus.$emit('treeMask',this.item);
            this.$set(this.item,'parent', this.parent);
            this.$set(this.item,'checked', false);
	    },
        methods: {
            goToCard() {
                if (this.editableFields.parameters['card_l'])
                    this.$bRouter.push(this.editableFields.parameters['card_url'] + "/" + this.item.id);
            },
            toggleLevel(element, e) {
                if(this.animation) return false;
                if(element.show === false) element.show = true;
                !element.hasOwnProperty('visible') ? this.$set(element, 'visible', false) : false;
                    element.visible = !element.visible;

            },

            beforeEnter(el) {
                this.animation = true;
                el.style.height = '0';
                el.style.opacity = 0;
            },

            enter(el,done) {
                Velocity(
                    el,
                    { opacity: 1, height: el.scrollHeight },
                    {complete: (element)=> {
                        setTimeout(()=>{
                            el.style.height = 'auto';
                            setTimeout(()=>{this.animation = false;},500)
                            done();
                        },250)
                    }}
                )
            },

            afterEnter(el,done) {
                Velocity(
                    el,
                    { overflow: 'visible', height: "auto" },
                )
            },

            beforeLeave(el) {
                this.animation = true;
                el.style.height = `${el.scrollHeight}px`;
            },

            leave(el,done) {
                this.$nextTick(e=>{
                    Velocity(
                        el,
                        { opacity: 0, height: 0,overflow: 'hidden' },
                        {complete: ()=> {
                                setTimeout(()=>{this.animation = false;},500)
                            }}
                    )
                })
            },
	        selectLevel(){
                let self = this;
                let formParameters = this.$store.getters.formParameters;
                let deepSelect = formParameters.select_in_deep === true &&
	                             formParameters.select_in_level !== true;
                let showChecked = formParameters.show_checked;
                let modeAdd = !this.item.checked;
                this.item.checked = !this.item.checked;
                send({[this.model_name] : [this.item.id]});
                
                deepSelect ? deepUnder(this.itemChildren,this.item, this.depth) : deepLevel(this.itemChildren,this.item);
                
                function showCheckedItem(element) {
                    !element.hasOwnProperty('visible') ? self.$set(element, 'visible', false) : false;
                    element.visible = true;
                }
                
                function getIDsAndSend(item,model) {
                    showChecked ? showCheckedItem(item) : false;
                    let itemsModel = item[model].map(e=>{
                        if(e.checked !== modeAdd){
                            e.checked = modeAdd;
                            return e.id
                        }
                    });
                    itemsModel.length > 0 && typeof itemsModel[0] !== 'undefined'?
                        send({[model] : itemsModel}) :
                        false
                }
                
                function deepUnder(itemChildren,item,level) {
	                
                    [...itemChildren].forEach(model=>{
                        let itemLocal;
                        let modelExist = item.hasOwnProperty(model);
                        if (!modelExist) return false;
                        itemLocal = item[model];
                        getIDsAndSend(item,model);
                        [...itemLocal].forEach(itemL=>{
                            deepUnder(itemChildrens(level+1,itemL),itemL,level+1)
                        })
                    })
                }
                
                function deepLevel(itemChildren,item) {
                    [...itemChildren].forEach(model=>{
                        let modelExist = item.hasOwnProperty(model);
                        if(!modelExist) return false;
                        getIDsAndSend(item,model);
                    })
                }
                
                function itemChildrens(depth,item){
                    let modelsChildren = self.$store.getters.getDepthParameters(depth)['children_models'];
                    let modelsChild = [];
                    modelsChildren.forEach(model => {
                        if (item.hasOwnProperty(model) && item[model].length > 0) modelsChild.push(model);
                    })

                    return modelsChild;
                }
                
		        // Записать/Удалить IDs
		        function send(data){
                    EventBus.$emit('treeSelectModel',{items:data,modeAdd})
                }
	        },
            editLine() {
                let data = {
                    modal: {
                        link: this.$bRoute.meta.route,
                        modal_title: 'title',
                        fields: this.editableFields.columns,
                        type: 'editRow',
                        model: {
                            ...this.item
                        },
                        addModels: [],
                        send_request: true,
                    }
                };
                EventBus.$emit('showModal', data)
                EventBus.$on('editFinish', (data) => {
                    console.log(data);
                    for (var k in data) {
                        // debugger;
                        this.item[k] = data[k];
                    }
                    EventBus.$off('editFinish');

                })
            },
            cellContent({column_code}, item) {
                var elems = this.editableFields.columns.filter(el => el.column_code === column_code);
                return elems.length > 0 ? item[elems[0]['key']] : null;

            }
        },
        computed: {
            element_depth() {
                return this.depth + 1
            },
            title() {
                let title = this.editableFields.parameters['model_title_field'];
                return this.item[title];
            },
            itemChildren() {
                let modelsChildren = this.$store.getters.getDepthParameters(this.depth)['children_models'];
                let modelsChild = [];
                modelsChildren.forEach(model => {
                    if (this.item.hasOwnProperty(model) && this.item[model].length > 0) modelsChild.push(model);
                })

                return modelsChild;
            },
            editableFields() {
                return this.$store.getters.getFieldsParameters[this.model_name];
            },

        },
        props: {
            item: {
                type: Object
            },
            depth: {
                type: Number
            },
            model_name: {
                type: String,
            },
	        parent:{
                type:Object
	        }
            // card_url:{
            //     type: String,
            //     default: '',
            // }

        },
        components: {
            TreeElement
        }
    }
</script>

